<template>
  <section class="pt-1 mx-3">
    <page-intro :introName="introName"></page-intro>
    <v-card>
      <v-card-title>
        <v-container>
          <v-layout row wrap>
            <v-flex xs12 sm6 md3 class="pr-3">
              <v-text-field
                clearable
                label="Target Account Name"
                v-model="TargetAccountName"
                @keyup.enter="GetAuditData()"
                prepend-icon="edit"
                single-line
                class="mr-4 input__font"
              ></v-text-field>
            </v-flex>
            <v-flex xs12 sm6 md3>
              <v-text-field
                clearable
                label="AD Admin Name"
                v-model="SubjectAccountName"
                @keyup.enter="GetAuditData()"
                prepend-icon="edit"
                single-line
                class="mr-4"
              ></v-text-field>
            </v-flex>
            <v-flex xs12 sm6 md3>
              <v-text-field
                clearable
                label="Group Name"
                v-model="GroupName"
                @keyup.enter="GetAuditData()"
                prepend-icon="edit"
                single-line
                class="mr-4"
              ></v-text-field>
            </v-flex>
          </v-layout>
          <v-layout row wrap>
            <v-flex xs6 sm6 md3>
              <datepicker :label="'From'" @setDateProp="setFromDate"></datepicker>
            </v-flex>
            <v-flex xs6 sm6 md3>
              <datepicker :label="'To'" @setDateProp="setToDate"></datepicker>
            </v-flex>
          </v-layout>
          <v-layout justify-space-between row wrap>
            <v-flex xs5 sm6 md8>
              <div class="pl-1">
                <v-progress-circular indeterminate color="primary" class="mt-3 ml-0" v-if="loading"></v-progress-circular>

                <v-btn
                  v-if="!loading"
                  color="tfgprimary"
                  class="mt-3 ml-0"
                  round
                  dark
                  small
                  @click="GetAuditData()"
                >Find</v-btn>
                <exportMenu v-if="adUsers.length > 0" :data="adUsers" :headers="headers"></exportMenu>
              </div>
            </v-flex>
            <v-flex xs7 sm6 md4>
              <v-text-field
                v-model="search"
                prepend-icon="filter_list"
                label="Filter results"
                single-line
                hide-details
              ></v-text-field>
            </v-flex>
          </v-layout>
        </v-container>
      </v-card-title>

      <v-data-table
        :headers="headers"
        :items="adUsers"
        :loading="false"
        item-key="id"
        :search="search"
        :rows-per-page-items="rowsperpage"
        class="px-2"
      >
        <v-progress-linear slot="progress" color="primary" indeterminate height="5"></v-progress-linear>

        <template slot="items" slot-scope="props">
          <tr>
            <td class="text-md-left pl-4">{{ props.item.targetAccountName }}</td>
            <td class="text-md-left pl-4">{{ props.item.subjectAccountName }}</td>
            <td class="text-md-left pl-4">{{ props.item.eventComment }}</td>
            <td class="text-md-left pl-4">{{ props.item.chgAttValue3 }}</td>
            <td class="text-md-left pl-4">{{ props.item.DoNotExpirePassword }}</td>
            <td class="text-md-left px-4">{{ props.item.chgAttGroupName }}</td>
            <td class="text-md-left px-4">{{ props.item.dateOccured }}</td>
          </tr>
        </template>
      </v-data-table>
    </v-card>
  </section>
</template>

<script>
import { GetADAuditData } from "@/services/api/ActiveDirectory";
import { Notify } from "@/services/HelperService";
import exportMenu from "@/components/exportMenu";
import PageIntro from "@/components/PageIntro";
import datepicker from "@/components/datepickerInMenu";
import { DataTableMixin } from "@/components/Mixins/DataTableMixin";

export default {
  mixins: [DataTableMixin],
  components: {
    exportMenu,
    PageIntro,
    datepicker
  },

  data() {
    return {
      introName: "Track Active Directory Changes",
      adUsers: [],
      search: "",
      SubjectAccountName: "",
      TargetAccountName: "",
      GroupName: "",
      fromDate: new Date(),
      toDate: new Date(),
      headers: [
        {
          text: "TargetAccount",
          align: "left",
          sortable: true,
          value: "targetAccountName",
          class: "pl-3"
        },

        {
          text: "AD Admin",
          align: "left",
          sortable: true,
          value: "subjectAccountName",
          class: ""
        },
        {
          text: "Changed",
          align: "left",
          sortable: true,
          value: "eventComment",
          class: ""
        },
        {
          text: "AccEnabled",
          align: "left",
          sortable: true,
          value: "chgAttValue3",
          class: ""
        },
        {
          text: "DoNotExpPass",
          align: "left",
          sortable: true,
          value: "DoNotExpirePassword",
          class: ""
        },
        {
          text: "ChgAttGroup",
          align: "left",
          sortable: true,
          value: "chgAttGroupName",
          class: ""
        },
        {
          text: "Date",
          align: "left",
          sortable: true,
          value: "dateOccured",
          class: ""
        }
      ]
    };
  },

  computed: {},

  methods: {
    GetAuditData() {
      this.loading = true;
      GetADAuditData({
        TargetName:
          this.TargetAccountName === null
            ? null
            : this.TargetAccountName.trim(),
        SubjectName:
          this.SubjectAccountName === null
            ? null
            : this.SubjectAccountName.trim(),
        GroupName: this.GroupName,
        FromDate: new Date(this.fromDate),
        ToDate: new Date(this.toDate)
      })
        .then(data => {
          console.log(data);
          this.adUsers = data;
        })
        .catch(error => {
          this.$notify(Notify(error));
        })
        .finally(() => {
          this.loading = false;
        });
    },
    setFromDate(value) {
      this.fromDate = value;
    },
    setToDate(value) {
      this.toDate = value;
    }
  }
};
</script>


