import { HTTP } from "@/main";

export const FindADUser = payload => {
  return HTTP.post("/activedirectory/FindUsers", {
    UserGroup: payload.UserGroup,
    Domain: payload.Domain
  }).then(response => {
    return response.data;
  });
};

export const FindADUserGroups = payload => {
  //console.log(payload);
  return HTTP.post("/activedirectory/GetUserGroups", {
    UserGroup: payload.UserGroup,
    Domain: payload.Domain
  }).then(response => {
    //console.log(response);
    return response.data;
  });
};

