<template>
  <v-menu
    ref="menu"
    :close-on-content-click="false"
    v-model="menu"
    :nudge-right="40"
    :return-value.sync="date"
    lazy
    transition="scale-transition"
    offset-y
    full-width
    min-width="290px"
  >
    <v-text-field slot="activator" v-model="date" :label="label" prepend-icon="event" class="mr-4"></v-text-field>
    <v-date-picker v-model="date" no-title scrollable>
      <v-spacer></v-spacer>
      <v-btn flat color="primary" @click="menu = false">Cancel</v-btn>
      <v-btn flat color="primary" @click="setDate">OK</v-btn>
    </v-date-picker>
  </v-menu>
</template>

<script>
export default {
  props: ["label"],

  data() {
    return {
      date: new Date().toISOString().substr(0, 10),
      menu: false
    };
  },
  methods: {
    setDate() {
      this.$refs.menu.save(this.date);
      this.$emit("setDateProp", this.date);
    }
  }
};
</script>
